* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  overflow-x: hidden;
}

::-webkit-scrollbar-track {
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #03c04b;
}

.mains {
  background: url(./components/pict/background-main.svg);
  background-repeat: no-repeat;
  background-size: cover;
}